import React from 'react';
import { string, shape } from 'prop-types';
import Link from 'gatsby-link';

import { Logo } from '../../components/Logo';
import { Button } from '../../ui';
import './404.scss';

export default function Page404({ data }){
  const { message, note, buttonText } = data.markdownRemark.frontmatter;

  return (
    <section className='page404__container'>
      <div className='logo animated'>
        <Logo
          id='logo-top'
          className='sh-logo'
          mode={'relaxed'}
        />
      </div>
      <h1
        className='small'
        dangerouslySetInnerHTML={{ __html: message }}
      />
      <div className='page404__note'>
        <p
          className='copytext'
          dangerouslySetInnerHTML={{ __html: note }}
        />
        <Link to='/'>
          <Button primary>
            {buttonText}
          </Button>
        </Link>
      </div>
    </section>
  );
}

Page404.propTypes = {
  data: shape({
    markDownRemark: shape({
      frontmatter: shape({
        title: string,
        path: string,
        message: string,
        note: string,
        buttonText: string
      })
    })
  })
};

export const query = graphql`
  query NotFoundPageQuery {
    markdownRemark(fields: { slug: { eq: "/404/" } }) {
      frontmatter {
        title
        path
        message
        note
        buttonText
      }
    }
  }
`;



